@mixin neomorph {
  background: var(--main-bg-color);
  box-shadow: 4px 4px 4px var(--dark-bg-color), -4px -4px 4px var(--light-bg-color);
}


div.slidersBox {
  position: relative;

  width: 100%;
  height: 80%;

  margin: 0 auto;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;

  div.slideInfo {
    align-items: center;

    p {
      padding: 0 5px;
    }

    input {
      margin: 0 10px;
      border: none;
      text-align: center;
      border-radius: 5px;
      background-color: transparent;
      font-family: var(--primary-font);


      box-shadow: 2px 2px 4px var(--dark-bg-color), -2px -2px 4px var(--light-bg-color);
    }

    span {
      width: 15px;
      height: 80%;
      border-radius: 3px;
    }
  }

  div.formatBox {
    height: 95%;
    width: 23%;

    margin: 5px 5px;
    padding: 0 5px;
    border-radius: 15px;

    display: flex;
    flex-direction: column;
    background-color: white;
    @include neomorph;

    &.colorListBox {
      width: 30%;
    }

    div.sliderBox {
      margin: 2px 0;

      flex-grow: 1;
      font-size: 14px;
      padding: 2% 0 1%;

      display: flex;
      flex-direction: column;

      justify-content: space-around;

      p {
        //height: 13px;
        margin: 0 0 0 5px;
      }

      .slide {
        position: relative;
        //background-color: white;
        border-radius: 8px;

        margin: 0 auto;
        width: 95%;

        max-height: 10px;
        flex-grow: 1;


        .slider {
          display: flex;
          justify-content: center;
          position: absolute;
          width: 5%;
          height: 100%;
          border-radius: 8px;

          //background-color: magenta;

          margin: auto 0;
          top: 0;
          bottom: 0;

          svg {
            transform: scale(1.5);
          }

          transition: left 50ms linear;
        }
      }
    }
  }
}
