.branchContainer {
  background-color: var(--main-bg-color);
  border-radius: 10px;
  background-color: #fdfdfd;
  box-shadow: 3px 3px 6px #e2e2e2, -3px -3px 6px #fefefe;
  //border: 1px solid gray;
  margin: 5px 3px;

  &:hover {
    background-color: var(--avg-branch-color);
  }

  .branchRoot {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    height: 45px;
    margin: 2px 0;


    .colTag {
      flex-grow: 0;
      width: 20px;
      height: 20px;
      border-radius: 4px;
      background-color: white;
      margin: 0 5px;
    }

    .climbDiv {
      flex-grow: 0;
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      align-items: center;

      .climbBtn {
        width: 12px;
        height: 12px;
        border-radius: 3px;
        background-color: white;
        margin: 0 2px;
      }
    }

    p {
      flex-grow: 1;
      margin: 5% 0;
    }

    .dropDiv {
      flex-grow: 0;
      margin-right: 5px;
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      align-items: center;
      height: 20px;
      padding: 5px;

      .dropBtn {
        width: 5px;
        height: 5px;
        border-radius: 5px;
        background-color: black;
        margin: 0 2px;
      }
    }
  }

  .branchLeaves {
    margin: 0 4px;
    padding-bottom: 3px;

    &.visible {
      display: block;
    }

    &.invisible {
      display: none;
    }
  }
}
